import { createContext, useCallback, useContext, useState } from "react";
import { fetchNoToken, fetchWithToken } from "../helpers/fetch.js";
import { ChatContext } from "../context/chat/ChatContext.jsx";
import { types } from "../types/types.js";

export const AuthContext = createContext();

const initialState = {
    uid: null,
    checking: true,
    logged: false,
    name: null,
    email: null
};

export const AuthProvider = ({ children }) => {

    const [auth, setAuth] = useState(initialState);
    const {dispatch} = useContext(ChatContext);

    const login = async (email, password) => {

        const resp = await fetchNoToken('login', { email, password }, 'POST');
        if (resp.ok) {
            localStorage.setItem('token', resp.token);
            const { user } = resp;
            setAuth({
                uid: user.uid,
                checking: false,
                logged: true,
                name: user.name,
                email: user.email
            });
        }
        return resp.ok;
    }

    const register = async (name, email, password) => {

        const resp = await fetchNoToken('login/new', { name, email, password }, 'POST');
        if (resp.ok) {
            localStorage.setItem('token', resp.token);
            const { user } = resp;
            setAuth({
                uid: user.uid,
                checking: false,
                logged: true,
                name: user.name,
                email: user.email
            });
        }
        return resp.msg;
    }

    const validateToken = useCallback(async () => {

        const token = localStorage.getItem('token');
        //si el token no existe
        if (!token) {
            setAuth({
                uid: null,
                checking: false,
                logged: false,
            })

            return false;
        }

        const resp = await fetchWithToken('login/renew');

        if (resp.ok) {
            localStorage.setItem('token', resp.token);
            const { user } = resp;
            setAuth({
                uid: user.uid,
                checking: false,
                logged: true,
                name: user.name,
                email: user.email
            });

            return true;

        } else {
            setAuth({
                uid: null,
                checking: false,
                logged: false,
            });

            return false;
        }

    }, [])

    const logout = () => {

        dispatch({
            type: types.logout
        });

        localStorage.removeItem('token');
        setAuth({
            checking: false,
            logged: false,
        });

    }
    
    return (
        <AuthContext.Provider value={{
            auth,
            login,
            register,
            validateToken,
            logout,
        }}>
            {children}
        </AuthContext.Provider>
    )
}
